    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    font-family: "Quicksand", sans-serif;

}

.poetsen-one-regular {
    font-family: "Poetsen One", sans-serif;
    font-weight: 400;
    font-style: normal;
  }


.quicksand {
  font-family: "Quicksand", sans-serif;
  font-optical-sizing: auto;
  font-weight: 200;
}
  

li {
    list-style: none;
}

h2 {
    margin: 20px;
    border-bottom: 3px solid black
}

.main-container {
    height: 100%;
}

.content-container {
    min-height: 100%;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: stretch;
}

.panel-header {
    display: flex;
    gap: 16px;
    background-color: lightcoral;
    height: 100px;
    /* box-shadow: 10px 5px 10px black; */
}

.panel-header > * {
    margin-left: 20px;
}

.project-header {
    display: flex;
    align-items: center;
}

.task-header {
    display: flex;
    align-items: center;

}

.add {
    min-width: 30px;
    min-height: 30px;
    border: 1px solid black;
    border-radius: 50px;
    margin: 8px;
    background-color: #f8f8f8;
    position: relative;
    top: 2px;
    left: 10px;

}

.add:hover {
    transition: all 0.5s ease;
    background-color: #a59f9f;
}

.project-sidebar {
    border-radius: 12px;
    background-color: #f8f8f8;
}

.main-panel {
    border-radius: 12px;
    background-color: white
}

.list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    gap: 16px;
    padding: 16px;
}

.to-do-item-box {
    display: grid;
    grid-template-columns: 100px 1fr 100px;
    align-items: center;

    background-color: rgb(219, 178, 108);
    border-radius: 16px;
    background-color: #e5e5e5;

}

/* .edit-btn {
    aspect-ratio: 1 / 1;
    justify-self: center;
    align-self: flex-start;
    margin-top: 8px;
    width: 30px;
    border-radius: 50%;
    background-color: lightcoral;
} */

.to-do-item {
    flex: 1;

    margin: 8px;
    padding: 8px;
    
    display: grid;
    
    grid-template-columns: 1fr 1fr;
    gap: 16px;
}

.to-do-field {
    background-color: white;
    padding: 8px;
    border-radius: 16px;
}

dialog {
    justify-self: center;
    align-self: center;
    padding: 16px;
}

.proj-button {
    width: 200px;
    padding: 8px;
    border-radius: 12px;
    background-color: #e5e5e583;
    transition: all 0.5s ease;
    margin-bottom: 6px;
}

.proj-button:hover {
    background-color: #a59f9f;

}

.project-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px;

}

a {
    position: relative;
    background-color: lightcoral;
    color: black;
    text-decoration: none;
    text-transform: uppercase;
    border-radius: 50%;
    aspect-ratio: 1 / 1;
    max-width: 40px;
    margin-left: 16px;    

    display: flex;
    align-items: center;
    justify-content: center;

    transition: all .5s ease;
}

a:hover {
    font-size: 1.5em;
    max-width: 50px;
    margin-left: 10px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,YAAY;IACZ,oCAAoC;;AAExC;;AAEA;IACI,sCAAsC;IACtC,gBAAgB;IAChB,kBAAkB;EACpB;;;AAGF;EACE,oCAAoC;EACpC,yBAAyB;EACzB,gBAAgB;AAClB;;;AAGA;IACI,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ;AACJ;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,4BAA4B;IAC5B,aAAa;IACb,qCAAqC;AACzC;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;;AAEvB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;IACX,yBAAyB;IACzB,kBAAkB;IAClB,QAAQ;IACR,UAAU;;AAEd;;AAEA;IACI,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;IACnB;AACJ;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sCAAsC;IACtC,mBAAmB;;IAEnB,oCAAoC;IACpC,mBAAmB;IACnB,yBAAyB;;AAE7B;;AAEA;;;;;;;;GAQG;;AAEH;IACI,OAAO;;IAEP,WAAW;IACX,YAAY;;IAEZ,aAAa;;IAEb,8BAA8B;IAC9B,SAAS;AACb;;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,oBAAoB;IACpB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,2BAA2B;IAC3B,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;;AAE7B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;;AAEjB;;AAEA;IACI,kBAAkB;IAClB,4BAA4B;IAC5B,YAAY;IACZ,qBAAqB;IACrB,yBAAyB;IACzB,kBAAkB;IAClB,mBAAmB;IACnB,eAAe;IACf,iBAAiB;;IAEjB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;;IAEvB,wBAAwB;AAC5B;;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nhtml, body {\n    height: 100%;\n    font-family: \"Quicksand\", sans-serif;\n\n}\n\n.poetsen-one-regular {\n    font-family: \"Poetsen One\", sans-serif;\n    font-weight: 400;\n    font-style: normal;\n  }\n\n\n.quicksand {\n  font-family: \"Quicksand\", sans-serif;\n  font-optical-sizing: auto;\n  font-weight: 200;\n}\n  \n\nli {\n    list-style: none;\n}\n\nh2 {\n    margin: 20px;\n    border-bottom: 3px 